"use client";

import React, { useEffect, useRef, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import UserFormModal, { UserFormData } from "./UserFormModal";
import { UsersService } from "@/services/users.service";
import toast from "react-hot-toast";

/* =========================
   Types
========================= */
interface UserDto {
  id: string;
  fullName: string;
  email: string;
  phone: string;
  userName: string;
  role: string;
  gender: string;
  address: string;
  isactive: boolean;
  createdBy?: string;
}

/* =========================
   Action Menu
========================= */
function ActionMenu({
  openUp,
  onEdit,
}: {
  openUp?: boolean;
  onEdit: () => void;
}) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    function handleClickOutside(e: MouseEvent) {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    }
    if (open) document.addEventListener("mousedown", handleClickOutside);
    return () =>
      document.removeEventListener("mousedown", handleClickOutside);
  }, [open]);

  return (
    <div ref={ref} className="relative">
      <button
        onClick={() => setOpen((p) => !p)}
        className="rounded-full p-2 hover:bg-gray-100 dark:hover:bg-gray-700"
      >
        ‚ãÆ
      </button>

      {open && (
        <div
          className={`absolute right-0 z-50 w-36 rounded-lg border bg-white shadow-lg
          dark:border-gray-700 dark:bg-gray-900
          ${openUp ? "bottom-full mb-2" : "top-full mt-2"}`}
        >
          <button
            onClick={() => {
              onEdit();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm hover:bg-gray-50 dark:hover:bg-gray-800"
          >
            ‚úèÔ∏è Edit
          </button>

          <button
            onClick={() => setOpen(false)}
            className="flex w-full gap-2 px-4 py-2 text-sm text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20"
          >
            üóë Delete
          </button>
        </div>
      )}
    </div>
  );
}

/* =========================
   User Table (REAL DATA)
========================= */
export default function UserTable() {
  const [users, setUsers] = useState<UserDto[]>([]);
  const [search, setSearch] = useState("");
  const [openModal, setOpenModal] = useState(false);
  const [mode, setMode] = useState<"add" | "edit">("add");
  const [selectedUser, setSelectedUser] =
    useState<UserFormData | undefined>();
  const [loading, setLoading] = useState(false);

  /* =========================
     Fetch Users
  ========================= */
  async function loadUsers() {
    setLoading(true);
    try {
      const res = await UsersService.getAll();
      setUsers(res.data.data);
    } catch {
      toast.error("Failed to load users");
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    loadUsers();
  }, []);

  /* =========================
     Search Filter
  ========================= */
  const filteredUsers = users.filter(
    (u) =>
      u.fullName.toLowerCase().includes(search.toLowerCase()) ||
      u.email.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="w-full rounded-xl bg-white px-6 pb-6 dark:bg-gray-900">
      <h2 className="mb-4 text-xl font-semibold text-gray-800 dark:text-white">
        Users
      </h2>

      {/* Top Bar */}
      <div className="mb-4 flex items-center justify-between">
        <Input
          placeholder="Search users..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-64"
        />
        <Button
          size="sm"
          onClick={() => {
            setMode("add");
            setSelectedUser(undefined);
            setOpenModal(true);
          }}
        >
          + Add User
        </Button>
      </div>

      {/* Table */}
      <div className="overflow-x-auto">
        <table className="w-full border-collapse">
          <thead className="bg-gray-50 dark:bg-gray-800">
            <tr className="text-sm text-gray-600 dark:text-gray-300">
              <th className="py-3 text-left">User</th>
              <th>Email</th>
              <th>Phone</th>
              <th>Address</th>
              <th>Role</th>
              <th>Status</th>
              <th className="text-right">Action</th>
            </tr>
          </thead>

          <tbody>
            {loading ? (
              <tr>
                <td colSpan={7} className="py-6 text-center text-sm">
                  Loading users...
                </td>
              </tr>
            ) : (
              filteredUsers.map((user, index) => (
                <tr
                  key={user.id}
                  className="border-b hover:bg-gray-50 dark:hover:bg-gray-800"
                >
                  <td className="py-4">
                    <p className="font-medium">{user.fullName}</p>
                    <p className="text-xs text-gray-500">{user.userName}</p>
                  </td>

                  <td>{user.email}</td>
                  <td className="font-mono">{user.phone}</td>
                  <td>{user.address || "-"}</td>

                  <td>
                    <span className="rounded-full bg-blue-100 px-3 py-1 text-xs font-semibold text-blue-700">
                      {user.role}
                    </span>
                  </td>

                  <td>
                    <span
                      className={`text-xs font-semibold ${
                        user.isactive
                          ? "text-green-600"
                          : "text-red-600"
                      }`}
                    >
                      {user.isactive ? "Active" : "Inactive"}
                    </span>
                  </td>

                  <td className="text-right">
                    <ActionMenu
                      openUp={index === filteredUsers.length - 1}
                      onEdit={() => {
                        setMode("edit");
                        setSelectedUser({
                          name: user.fullName,
                          email: user.email,
                          phone: user.phone,
                          userName: user.userName,
                          role: user.role,
                          gender: user.gender,
                          password: "",
                          confirmPassword: "",
                        });
                        setOpenModal(true);
                      }}
                    />
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/* Modal */}
      <UserFormModal
        open={openModal}
        mode={mode}
        initialData={selectedUser}
        onClose={() => setOpenModal(false)}
        onSubmit={() => {
          loadUsers();
          setOpenModal(false);
        }}
      />
    </div>
  );
}
